import React from 'react';

import shoWall from './images/sho-brick-wall.svg';
import shoWallSmall from './images/sho-brick-wall-small.svg';

const metadata = {
    title: 'Constant-Period Oscillation implies SHM',
    date: [2020, 7, 29],
    tags: ['physics']
}

export default function() {
    return <>
        <p>It’s well-known that the period of a simple harmonic oscillator (SHO) has a period independent of its oscillation amplitude.
            But is this the only oscillator for which this holds?</p>

        <p>No. A simple counterexample is the SHO + ‘brick wall’ potential:</p>
        <img
            className='desktop'
            alt='Right half of a quadratic potential, with a delta function at zero.'
            src={shoWall}
        />
        <img
            className='mobile'
            alt='Right half of a quadratic potential, with a delta function at zero.'
            src={shoWallSmall}
        />
    </>;
}

export {metadata};
